import styled from "styled-components"

export const MainNav = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1030;
  backdrop-filter: saturate(180%) blur(24px);
  background-color: var(--background-primary-blur);
  box-shadow: inset 0px -1px 0px var(--separator-non-opaque);
  @media print {
    display: none;
  }
  nav {
    font-size: 17px;
    line-height: 24px;
    display: flex;
    min-height: 48px;
    .main-nav-left {
      display: flex;
      height: 48px;
      opacity: 0.8;
      color: var(--hight-contrast);
      align-items: center;
      text-decoration: none;
      white-space: nowrap;
      transition: opacity 0.15s ease-in-out;
      :hover {
        opacity: 1;
      }
      svg {
        fill: var(--hight-contrast);
      }
    }
    .main-nav-center {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 48px;
      max-width: 1200px;
      margin: 0 48px;
      user-select: none;
      a {
        opacity: 0.8;
        display: flex;
        align-items: center;
        height: 100%;
        color: var(--hight-contrast);
        text-decoration: none;
        transition: opacity 0.15s ease-in-out;
        :hover {
          opacity: 1;
        }
        /* :not(:last-child) {
          margin-right: 24px;
        } */
      }
      @media (max-width: 1200px) {
        display: none;
      }
    }
    .main-nav-right {
      display: flex;
      height: 48px;
      align-items: center;
      cursor: pointer;
      i {
        :not(:first-child) {
          margin-left: 16px;
        }
      }
      @media (max-width: 1200px) {
        display: none;
      }
    }
    @media (max-width: 1200px) {
      justify-content: center;
    }
  }
`
